import React from 'react'
import MainHeader from 'views/mobileView/components/molecules/Headers/MainHeader';
import FloatingTextInput from 'views/mobileView/components/atoms/FloatingTextInput';

export default function SetNewPin(props){
    const [pin,setPin]=React.useState('');
    const [nextDisabled,setNextDisabled]=React.useState(false)
    const TYPE = props.location.state.type
    React.useEffect(() => {
        if(!props.location.state?.type){
			props.history.push('/menu')
		}
	}, []);
    React.useEffect(() => {
		checkValidation();
	}, [pin]);
    const checkValidation = () => {
            if (pin && pin.length === 4 ) {
                setNextDisabled(false);
            } else {
                setNextDisabled(true);
            }  
    };

    return (
        <div className='mainContainer'>
            <MainHeader
                header={TYPE==='new'?'Set MF transaction PIN':TYPE==='change'?'Change MF transaction PIN':''}
                navigation={props.history}
            />
            <div className="dataContainer" style={{paddingTop:'42px'}}>
                <FloatingTextInput
					type="tel"
					id="pin"
					label="Set new PIN"
					style={{ letterSpacing: '16px' }}
					autoFocus
					onChange={(e) => {
                        const newPin = e.target.value.replace(/\D/g, '')
						setPin(newPin)
					}}
					value={pin}
					maxLength={4}
				/>
            </div>
            
            <div className='footer'>
                <button
                    className='solidButton'
                    style={{ opacity: nextDisabled ? 0.4 :1 }}
                    navigation={props.history}
                    onClick={()=>props.history.push({
                        pathname: '/menu/mf-transaction-pin/confirm-pin',
                        state : {...props.location.state, newPin: pin}
                    })}
                    >
                    Confirm
                </button>
            </div>
        </div>
    )
}
